* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  position: relative;
  min-height: 100vh;
  width: 100vw;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 280px;
  height: 100%;
  background-color: #11101d;
  padding: 6px;
}

.sidebar .content .logo {
  color: #fff;
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
}

.content .logo i {
  font-size: 35px;
  margin-right: 10px;
}

.content .logo .logo-name {
  font-size: 26px;
  font-weight: 400;
}

.content .logo .img-tag {
  position: absolute;
  top: 70px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content .logo .img-tag img {
  width: 150px;
}

.sidebar #btn {
  position: absolute;
  color: white;
  left: 90%;
  top: 6px;
  font-size: 25px;
  height: 50px;
  width: 50px;
  text-align: center;
  line-height: 50px;
  transform: translateX(-50%);
}

.navlist i {
  margin-right: 10px;
}

.sidebar ul {
  margin-top: 220px;
  /* margin-left: 40px; */
}

.sidebar ul li {
  position: relative;
  list-style: none;
  height: 60px;
  width: 100%;
  margin: 0 5px;
  line-height: 50px;
  color: white;
}

