.sidebar ul li a {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: larger;
  transition: all 0.3s ease;
  border-radius: 12px;
}

.sidebar ul li a:hover {
  background-color: #fff;
  color: #11101d;
}

.sidebar ul li a:hover .link-name {
  color: #11101d;
}
.sidebar ul li a:hover .bx {
  color: #11101d;
}

.sidebar ul li a i {
  height: 50px;
  min-width: 50px;
  border-radius: 12%;
  line-height: 50px;
}

.sidebar .profile-content {
  position: absolute;
  color: #fff;
  bottom: 0px;
  left: 0px;
}

.sidebar .profile-content .profile {
  position: relative;
  padding: 10px 6px;
  height: 100px;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
}

.collpase-sidebar {
  position: fixed;
  width: 80px;
  height: 100vh;
  background-color: #11101d;
  z-index: 1000 !important;
}

.collpase-sidebar .menu {
  height: 50px;
  width: 100%;
}

.collpase-sidebar .menu i {
  color: #fff;
  font-size: 2.5rem;
  align-items: center;
  display: flex;
  justify-content: center;
  transform: translateY(13px);
}

.collpase-sidebar ul {
  margin-top: 4rem;
  list-style: none;
}

.collpase-sidebar ul li i {
  color: white;
  font-size: 2.5rem;
  margin-top: 40px;
  text-decoration: none;
  transform: translateX(-40%);
}
.collpase-sidebar ul li i:hover {
  background: white;
  color: #11101d;
  border-radius: 6px;
}
.collpase-sidebar ul li .tooltips {
  position: absolute;
  left: 80px;
  transform: translateY(-50px);
  border-radius: 6px;
  height: 35px;
  width: 122px;
  background: white;
  line-height: 35px;
  text-align: center;
  box-shadow: 2px 2px 5px 10px rgba(0, 0, 0, 0.2);
  transition: 0s;
  opacity: 0;
  pointer-events: none;
}

.collpase-sidebar ul li:hover .tooltips {
  transition: all 0.5s ease;
  opacity: 1;
  margin-top: 6rem;
}
